body {
  margin: 0;
  padding: 0;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
   
  background-color: rgb(33, 33, 33);
  height: 100%;
 
  margin: 0 auto;
  padding: 0 auto;
}

img{
  width: 100%;
  height: 100%;
  border-radius: 5px;
  /* transform: scale(0); */
  opacity: 0;
  animation: 2s hideImage linear;
}

h1{
  text-align: center;
  color: aliceblue;
}

.button {
  margin: 15px;
  background-color: #fff;
  border: none;
  /* width: 50vw; */
  /* height: 5vh; */
  border-radius: 10px;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-size: 5vw;

  /* background-color: #4CAF50; */
  /* border: none; */
  /* color: white; */
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}

.container{
  margin: 0 auto;
  width: 90%;
  height: 90%;
  max-width: 50rem;
  /* height: 18rem; */

  display: grid;
  grid-template-columns: repeat(4, 1fr);
  /* grid-template-rows: repeat(4, 1fr); */
  gap: .3rem;
  
}

.card{
  background-color: #fff;
  border-radius: 5px;

  animation: 2s hideCard linear;
}

.card.active{
  transform: rotateY(0);
  background-color: #008000;
}

.card.active img{
  opacity: 1;
}

.counter {
  color: #fff;
  margin: 10px;
  font-weight: bold;
}

.matched{
  background-color: rgb(25, 130, 225) !important;
  border-radius: 5px;
}

.unmatch{
    background-color: #fff;
    border-radius: 5px;
    animation: 2s hideCard linear;
}

@keyframes hideCard {
  0%, 70%{
    transform: rotateX(0deg);
  }100%{
    transform: rotateX(180deg);
  }
}

@keyframes hideImage {
  0%, 84%{
    opacity: 1;
  }85%{
    opacity: 0;
  }
}